import React, { Component } from 'react';
import './login.css'
import {Link} from 'react-router-dom'

//import logo
import logo from '../../assets/branging/logo.svg'
class login extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        return ( <div className="div-login">

                <img className="logo-sign-in" src={logo}/>

                <h1 className="h1-sign-in">Sign in to CodeStografo</h1>

                <div className="div-sign-in-user-input">

                    <h1>Username or email address</h1>
                    <input type="e-mail"></input>
                    <h1> Password <Link className="link-forgot-password">Forgot password?</Link> </h1>
                    < input type="password"></input>
                    <button className="button-Sing-in" type="button"><b>Sign in</b></button> 

                </div>

                <div className="div-new-to-create-account">
                    <h1> Don't have account ?  <Link className="Link-create-account">Create an account.</Link></h1>
                    
                </div>
            
        </div> );
    }
}
 
export default login;